<template>
    <div class="main-page">
        <List ref="list" :apis="apis"  :fieldToPage="fieldToPage" FormCode="oa_car">
            <el-button slot="contentRight" type="primary" @click="auditAny">批量审核</el-button>
            <el-button slot="contentRight" type="primary" @click="$refs.FormAE.start(false)">新增</el-button>
        </List>
        <!-- <FormAE ref="FormAE" :apis="apis" @refresh="$refs.list.getList()" FormsCode="oa_car" imgNameField="Icon" upLoadBeforSubmit />
        <FormDetails ref="FormDetails" :apis="apis" FormsCode="oa_car"></FormDetails> -->
    </div>
</template>

<script>
export default {
    name: "CarsList",
    props: {},
    components: {
        List: () => import("@/universalPage/list/List.vue"),
        // FormAE: () => import("@/universalPage/formAE/FormAE.vue"),
        // FormDetails: () => import("@/universalPage/formDetails/FormDetails.vue")
    },
    data() {
        return {
            apis: {
                edit: this._api.VMS_CarEdit,
                create: this._api.VMS_CarCreate,
                list: this._api.VMS_CarManage,
                delete: this._api.VMS_CarDelete,
                audit: this._api.VMS_CarAudit,
                details: this._api.VMS_CarDetails
            },
            fieldToPage: {
                FName: this.toDetails
            }
        }
    },
    methods: {
        toDetails(row) {
            this.$refs.FormDetails.start(row)
        },
        auditAny() {
            let list = this.$refs.list.chooseList
            if (list.length !== 0) {
                this.$u.confirm("请确认批量审核!").then(() => {
                    this.apis.audit(list.map(it => {
                        return {
                            Id: it.Id,
                            FStatus: 3
                        }
                    })).then(res => {
                        if (res) {
                            this.$u.msg("审核成功!")
                            this.$refs.list.getList()
                        }
                    })
                }).catch(() => { })
            }
        }
    },
    mounted() { }
}
</script>

<style lang="scss" scoped>
</style>